import React from 'react'
import styles from './DropDownMenu.module.css'
import cn from 'classnames'



const DropDownMenu = (props) => {

    const handlerSortByType = (type) => {
        props.SetPokemonsType(type)
        props.SetPokemonsLimit(96)
    }

    return (
        <nav className={styles.one}>
            <ul className={styles.topmenu}>
                <li><a>Type<i className={cn(styles.fa)}></i></a>
                    <ul className={styles.submenu}>
                        <li className={cn(styles.dropDownItem, styles.normal)}  onClick={() => props.SetPokemonsType(null)} >None</li>
                        <li className={cn(styles.dropDownItem, styles.normal)}  onClick={() => handlerSortByType("normal")} >Normal</li>
                        <li className={cn(styles.dropDownItem, styles.fighting)} onClick={() => handlerSortByType("fighting")} >Fighting</li>
                        <li className={cn(styles.dropDownItem, styles.flying)} onClick={() => handlerSortByType("flying")} >Flying</li>
                        <li className={cn(styles.dropDownItem, styles.poison)} onClick={() => handlerSortByType("poison")} >Poison</li>
                        <li className={cn(styles.dropDownItem, styles.ground)} onClick={() => handlerSortByType("ground")} >Ground</li>
                        <li className={cn(styles.dropDownItem, styles.rock)} onClick={() => handlerSortByType("rock")} >Rock</li>
                        <li className={cn(styles.dropDownItem, styles.bug)} onClick={() => handlerSortByType("bug")} >Bug</li>
                        <li className={cn(styles.dropDownItem, styles.ghost)} onClick={() => handlerSortByType("ghost")} >Ghost</li>
                        <li className={cn(styles.dropDownItem, styles.steel)} onClick={() => handlerSortByType("steel")} >Steel</li>
                        <li className={cn(styles.dropDownItem, styles.fire)} onClick={() => handlerSortByType("fire")} >Fire</li>
                            <ul className={styles.submenu2}>
                                <li className={cn(styles.dropDownItem, styles.water)} onClick={() => handlerSortByType("water")} >Water</li>
                                <li className={cn(styles.dropDownItem, styles.grass)} onClick={() => handlerSortByType("grass")} >Grass</li>
                                <li className={cn(styles.dropDownItem, styles.electric)} onClick={() => handlerSortByType("electric")} >Electric</li>
                                <li className={cn(styles.dropDownItem, styles.psychic)} onClick={() => handlerSortByType("psychic")} >Psychic</li>
                                <li className={cn(styles.dropDownItem, styles.ice)} onClick={() => handlerSortByType("ice")} >Ice</li>
                                <li className={cn(styles.dropDownItem, styles.dragon)} onClick={() => handlerSortByType("dragon")} >Dragon</li>
                                <li className={cn(styles.dropDownItem, styles.dark)} onClick={() => handlerSortByType("dark")} >Dark</li>
                                <li className={cn(styles.dropDownItem, styles.fairy)} onClick={() => handlerSortByType("fairy")} >Fairy</li>
                                <li className={cn(styles.dropDownItem, styles.unknown)} onClick={() => handlerSortByType("unknown")} >Unknown</li>
                                <li className={cn(styles.dropDownItem, styles.shadow)} onClick={() => handlerSortByType("shadow")} >Shadow</li>
                            </ul>
                    </ul>

                </li>
            </ul>
        </nav>
    )
}

export default DropDownMenu